.wrapper {
  z-index: 5;
  font-size: 30px;
  margin: 30px 0 50px 30px;
  color: #fff;
  display: grid;
  gap: 20px;
}

.weatherDetails {
  p {
    text-transform: capitalize;
    font-size: 40px;
  }
}

.weatherToday {
  font-size: 30px;
  display: flex;
  align-items: center;
  gap: 10px;

  h3,
  p,
  img {
    font-size: 35px;
    margin: 5px;
  }
}

@media (max-width: 1300px) {
  .wrapper {
    display: flex;
  }
  .weatherToday {
    font-size: 20px;
    gap: 10px;

    h3,
    p,
    img {
      font-size: 25px;
      margin: 0;
    }
  }
}

@media (max-width: 725px) {
  .wrapper {
    flex-direction: column;
    align-items: center;
  }
  .weatherToday {
    flex-direction: column;
    gap: 2px;

    h3,
    p,
    img {
      font-size: 25px;
      margin: 0px;
    }
  }
}
